<template>
  <div id="login">
    
    <div class="card-login form">
      <div class="header">
        <h2></h2>
      </div>
      <div class="body">
        <div class="msgs">
          <span v-if="loading" class="yellow msg">...</span>
          <!-- <span v-else-if="errorMsg" class="icon iconfont iconnotice red msg" v-text="errorMsg"></span> -->
        </div>
        <div class="row">
          <input ref="username" id="username" placeholder="" type="text" v-model="form.username">
        </div>
        <div class="row">
          <input ref="password" id="password" placeholder="" type="password" v-model="form.password">
        </div>
      </div>
      <!-- <div class="row">
        <label for="captcha"></label>
        <input id="captcha" type="text" v-model="form.tryCode">
        <img alt="" src="/api/defaultKaptcha" @click="getCaptcha()" ref="captcha"/>
      </div> -->
      <div class="footer">
        <span class="btn btn-yellow login" @click="onSubmit"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      form: {
        username: "",
        password: ""
      },
      loading:false,
      //errorMsg:''
    };
  },
  methods: {
    onSubmit() {
      if(!this.form.username){
        //this.errorMsg='';
        this.$toast({
          showCancel:false,
          t1:'',//
          t2: '',//
          type:'error'
        });
        this.$refs['username'].focus();
      }else if(!this.form.password){
        //this.errorMsg='';
        this.$toast({
          showCancel:false,
          t1:'',//
          t2: '',//
          type:'error'
        });
        this.$refs['password'].focus();
      }else{
        this.loading = true;
        //this.errorMsg='';
        let data = this.$qs.stringify(this.form);
        let that = this;
        this.$axios({
            method:'post',
            url:'/api/admin/login',
            data: data
        }).then((response) =>{
            console.log('');
            console.log(response.data);
            let info = response.data;
            if(info.flag){
              let admin = info.data;
              that.$store.commit('setAdmin',admin);
              that.$router.push('/admin/records');
              this.$toast({
                showCancel:false,
                t1:'',//
                t2: '<b>'+admin.nickname+'</b>',//
                type:'success'
              });
            }else{
              //that.errorMsg = info.errorMsg;
              this.$toast({
                showCancel:false,
                t1:'',//
                t2: info.errorMsg,//
                type:'error'
              });
            }
            that.loading = false;
        }).catch((error) => {
            this.$toast({
                showCancel:false,
                t1:'',//
                t2:'',//
                type:'error'
              });
            that.loading = false;
        });
      }
    },
  },
};
</script>

