      }
      .row{
        margin-top: 1rem;
        input{
          display: block;
          box-sizing: border-box;
          width: 100%;
          background-color: @c_bg;
          height: 2.4rem;
          border-radius: 1.2rem;
          line-height: 2.4rem;
          padding: 0 1rem;
          font-size: 1rem;
          border: none;
          outline: none;
          color: @c2;
          text-align: center;
          &:focus{
            border: none;
          }
        }
      }
    }
    .footer{
      margin-top: 1rem;
      .login{
        display: block;
        text-align: center;
      }
    }
  }
}
